<style scoped lang="less">
  .profit-page {

    .top {
      display: flex;
      align-items: center;
      padding: 10px 0;

      .top-item {
        position: relative;
        top: 0;
        left: 0;
        margin-left: 10px;
      }

      .chooseFile {
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }

    .pagination {
      margin-top: 10px;
    }
  }
</style>

<template>
  <div class="profit-page">
    <div class="top">
      <!-- 日期选择器 -->
      <el-date-picker
        class="top-item"
        format="yyyy 年 MM 月 dd 日"
        value-format="yyyy-MM-dd"
        v-model="time"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
      <!-- 查询 -->
      <el-button class="top-item" @click="searchTableData">查询</el-button>
      <!-- 导出 -->
      <el-button class="top-item" type="success" @click="exportTableData">导出</el-button>
      <!-- 导入 -->
      <el-button class="top-item" type="primary" v-show="showImportBtn">
        <!-- hide -->
        <input type="file" class="chooseFile" ref="chooseFile"/>
        导入
      </el-button>
      <!-- 批量删除 -->
      <el-button class="top-item" type="danger" v-if="showImportBtn" @click="deleteProfitDataFromId">批量删除</el-button>
    </div>

    <!-- 表格数据 -->
    <el-table
      @select-all="tableSelect"
      @select="tableSelect"
      :data="tableData.data"
      border
      style="width: 100%">

      <el-table-column
        align="center"
        type="selection"
        width="36">
      </el-table-column>

      <el-table-column
        align="center"
        prop="channelId"
        label="渠道号"
        width="180">
      </el-table-column>

      <el-table-column
        align="center"
        prop="endMoney"
        width="180"
        label="结算款">
      </el-table-column>

      <el-table-column
        align="center"
        prop="news"
        width="180"
        label="新增">
      </el-table-column>

      <el-table-column
        align="center"
        prop="info"
        width="180"
        label="信息费">
      </el-table-column>

      <el-table-column
        align="center"
        prop="date"
        width="180"
        label="日期">
      </el-table-column>

      <!-- 操作选项 -->
      <el-table-column width="150" align="center" prop="operate" label="操作选项">
        <template slot-scope="scope">
          <el-button size="mini" type="success" @click="showEditProfitNews(scope.$index)">修改</el-button>
        </template>
      </el-table-column>

    </el-table>
    <!-- 底部按钮 -->
    <el-pagination
      class="pagination"
      @size-change="tableDataSizeChange"
      @current-change="tableDataCurrentChange"
      :current-page="tableData.current_page"
      :page-sizes="[10, 20]"
      :page-size="tableData.page_size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData.total">
    </el-pagination>

    <!-- 新增提示框 -->
    <el-dialog class="editProfitNewsDialog" center width="600px" title="修改分包数据" :visible.sync="editProfitNewsDialog.show">
      <!-- 用户名 -->
      <el-row style="margin-top: 10px;" type="flex" align="middle" justify="center">
        <el-col :offset="3" :span="3">渠道号</el-col>
        <el-col :span="12">
          <el-input class="newUserInput" v-model="editProfitNewsDialog.channelId" placeholder="请输入内容">
          </el-input>
        </el-col>
      </el-row>
      <!-- 结算款 -->
      <el-row style="margin-top: 10px;" type="flex" align="middle" justify="center">
        <el-col :offset="3" :span="3">结算款</el-col>
        <el-col :span="12">
          <el-input v-model="editProfitNewsDialog.endMoney" class="newUserInput" placeholder="请输入内容">
          </el-input>
        </el-col>
      </el-row>
      <!-- 新增 -->
      <el-row style="margin-top: 10px;" type="flex" align="middle" justify="center">
        <el-col :offset="3" :span="3">新增</el-col>
        <el-col :span="12">
          <el-input v-model="editProfitNewsDialog.news" class="newUserInput" placeholder="请输入内容">
          </el-input>
        </el-col>
      </el-row>
      <!-- 信息费 -->
      <el-row style="margin-top: 10px;" type="flex" align="middle" justify="center">
          <el-col :offset="3" :span="3">信息费</el-col>
        <el-col :span="12">
          <el-input v-model="editProfitNewsDialog.info" class="newUserInput" placeholder="请输入内容">
          </el-input>
        </el-col>
      </el-row>
      <!-- 日期 -->
      <el-row style="margin-top: 10px;" type="flex" align="middle" justify="center">
        <el-col :offset="3" :span="3">日期</el-col>
        <el-col :span="12">
          <el-input v-model="editProfitNewsDialog.date" class="newUserInput" placeholder="请输入内容">
          </el-input>
        </el-col>
      </el-row>

      <!-- 页脚 -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="clearEditProfitNews">清 空</el-button>
        <el-button type="primary" @click="addEditProfitNews">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  deleteGameProfitNews, editProfitNews,
  exportTableData,
  gameProfitNews,
  searchGameProfitNews,
  uploadFile
} from '@/utils/api'

export default {
  name: 'profit-page',
  data () {
    return {
      time: null, // 选择日期的时间
      tableData: {
        data: [],
        page_size: 10, // 页面数据个数
        current_page: 1, // 当前 index
        total: 0
      },
      showImportBtn: false, // 显示导入按钮
      deleteIds: [], // 保存选择的元素的id
      editProfitNewsDialog: {
        show: false,
        channelId: '',
        news: '',
        info: '',
        date: '',
        endMoney: '',
        id: ''
      }
    }
  },

  mounted () {
    // 获取个人信息
    const user = JSON.parse(localStorage.getItem('user'))

    this.showImportBtn = user.loginName === 'admin'

    // 加载表格数据
    this.loadTableData()

    this.$refs.chooseFile.onchange = async (e) => {
      try {
        const res = await uploadFile({ file: e.target.files[0] })
        await this.$checkData(res.data)

        // 导入成功
        this.$message({
          showClose: true,
          message: '导入成功',
          type: 'success'
        })

        this.loadTableData()
      } catch (e) {
        console.log(e)
      }
    }
  },

  methods: {
    // 导出表格数据
    exportTableData () {
      exportTableData(this.time && this.time[0], this.time && this.time[1])
    },

    // 选中用户
    tableSelect (selection) {
      // 保存删除 dom 的下标 和 id
      this.deleteIds = []

      for (const s of selection) {
        this.deleteIds.push(s.id)
      }
    },

    // 显示修改分包数据弹窗
    showEditProfitNews (index) {
      // 显示已经有的数据
      const item = this.tableData.data[index]
      this.editProfitNewsDialog.show = true
      this.editProfitNewsDialog.channelId = item.id
      this.editProfitNewsDialog.news = item.news
      this.editProfitNewsDialog.info = item.info
      this.editProfitNewsDialog.date = item.date
      this.editProfitNewsDialog.endMoney = item.endMoney
      this.editProfitNewsDialog.id = item.id
    },

    // 修改分包数据
    async addEditProfitNews () {
      try {
        const res = await editProfitNews({
          id: this.editProfitNewsDialog.id,
          channelId: this.editProfitNewsDialog.channelId,
          newAdd: this.editProfitNewsDialog.news,
          messageFee: this.editProfitNewsDialog.info,
          accountcleark: this.editProfitNewsDialog.endMoney,
          recordTime: this.editProfitNewsDialog.date
        })

        await this.$checkData(res.data)

        // 修改成功
        this.$message({
          showClose: true,
          message: '修改成功',
          type: 'success'
        })

        // 关闭弹窗
        this.editProfitNewsDialog.show = false

        // 清空数据
        this.clearEditProfitNews()

        // 重新获取 data
        this.loadTableData()
      } catch (e) {
        console.log(e)
      }
    },

    // 清空修改分包数据
    clearEditProfitNews () {
      this.editProfitNewsDialog.channelId = ''
      this.editProfitNewsDialog.news = ''
      this.editProfitNewsDialog.info = ''
      this.editProfitNewsDialog.date = ''
      this.editProfitNewsDialog.endMoney = ''
    },

    // 删除报表数据
    async deleteProfitDataFromId () {
      try {
        const res = await deleteGameProfitNews(this.deleteIds.join(','))
        await this.$checkData(res.data)

        // 删除成功
        this.$message({
          showClose: true,
          message: '删除成功',
          type: 'success'
        })

        this.loadTableData()
      } catch (e) {
        console.log(e)
      }
    },

    // 加载表格数据
    async loadTableData () {
      try {
        const res = await gameProfitNews(this.tableData.current_page, this.tableData.page_size)
        const data = await this.$checkData(res.data)

        this.updateTableData(data)
      } catch (e) {
        console.log(e)
      }
    },

    // 搜索表格数据
    async searchTableData () {
      const res = await searchGameProfitNews(this.tableData.current_page, this.tableData.page_size, this.time && this.time[0], this.time && this.time[1])
      const data = await this.$checkData(res.data)

      this.updateTableData(data)
    },

    // 更新表格数据
    updateTableData (data) {
      this.tableData.data = []

      this.tableData.total = data.total
      data.rows.forEach((item) => {
        this.tableData.data.push({
          id: item.id,
          date: item.recordTime,
          endMoney: item.accountcleark,
          info: item.messageFee,
          news: item.newAdd,
          channelId: item.channelId
        })
      })
    },

    // 分页数据
    tableDataCurrentChange (index) {
      // 记录当前 page
      this.tableData.current_page = index
      this.loadTableData()
    },

    // 一页数据
    tableDataSizeChange (index) {
      this.tableData.page_size = index
      this.loadTableData()
    }
  }
}
</script>
